.tableContainer{padding:1.5rem;background-color:#fff;border-radius:10px;box-shadow:0 2px 8px rgba(0,0,0,.1);overflow-x:auto}.tableContainer h2{font-size:1.25rem;margin-bottom:1rem;color:#333}.tableContainer table{width:100%;border-collapse:collapse;font-size:.95rem}.tableContainer table thead{background-color:#f4f4f4}.tableContainer table thead th{text-align:left;padding:.75rem;font-weight:600;color:#555;border-bottom:2px solid #ddd}.tableContainer table tbody tr{border-bottom:1px solid #eee}.tableContainer table tbody tr:hover{background-color:#f9f9f9}.tableContainer table tbody tr td{padding:.75rem;color:#444}